html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {    
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.4;    
}
p {    
    font-size: 2rem;
}
a {
    text-decoration: none;
}

img,
picture {
   width: 100%;
   display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}

h1, h2, h3 {
    font-family: $fuente_principal;
    margin: calc($separacion / 2) 0;
    font-weight: 700;
    
    
}

h1 {
    font-size: 4.4rem;
}
h2 {
    font-size: 3.8rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}

[class*="contenedor"] {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    
}

[class$="heading"] {
    margin: 2rem 0;
}
