.cita {
    background-image: url(../img/bg_citas.jpg);
    background-position: center, center;
    background-size: cover;
    background-attachment: fixed;
    padding: 5rem 0;
    text-align: center;
    &__heading {
        font-weight: 400;
        color: $blanco;
        font-size: 5rem;
    }
    &__texto {
        max-width: 60rem;
        margin: 5rem auto;
        text-align: center;
        color: $blanco;
        line-height: 2;
    }
    &__enlace {
        @include boton( $primario, $blanco);
        padding: 2rem 10rem;
        
    }
}