.slider {
    overflow: hidden;
    position: relative;

    &__slide {
        height: 100% ;
        width: 100%;
        flex-shrink: 0;
        position: relative;
        transition-property: transform;
    }

    &__imagen {
        width: 100%;
    }

    .swiper-button-next,
    .swiper-button-prev {
        color: $blanco;        
        width: 4rem;          
              
        @include tablet {
            width: 6rem;
        }
        @include desktop {
            width: 8rem;
        }
    }
    .swiper-button-next::after, 
    .swiper-button-prev::after {
        font-size: 2.5rem ;        
        @include tablet {
            font-size: 3rem ;
        }
    }

    .swiper-pagination-bullet {
        background-color: $blanco;
        opacity: .5;
    }
    .swiper-pagination-bullet-active {
        background-color: $primario;
    }
}