.footer {
    padding-top: 5rem;    

    &__grid {
        @include tablet {
            @include grid(3 , 2rem);
        }
    }
    &__widget {
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
       
    }
    &__heading {
        @include heading;
        font-size: 3.4rem;
        text-align: left;
    }
    &__texto {
        line-height: 1.8;
        text-align: center;
        color: $negro;
        @include tablet {
            text-align: left;
        }
    }

    &__copyright {
        text-align: center;
        max-width: 120rem;
        margin: 2rem auto 0 auto;
        border-top: .1rem solid darken( $gris, 10%) ;
        font-size: 1.6rem;
        padding: 2rem;
        color:  $grisOscuro;
    }
}