/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.4;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.8rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

[class*=contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

[class$=heading] {
  margin: 2rem 0;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-primario {
  background-color: #2d6499;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
}
.boton-primario:hover {
  background-color: #275785;
  cursor: pointer;
}

.boton-secundario-block {
  background-color: #4d4c52;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
}
.boton-secundario-block:hover {
  background-color: #414045;
  cursor: pointer;
}

.boton-secundario {
  background-color: #4d4c52;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
}
.boton-secundario:hover {
  background-color: #414045;
  cursor: pointer;
}

.header {
  padding: 2rem;
  background-color: #f8f8f8;
  border-bottom: 0.1rem solid #dfdfdf;
  margin-bottom: 3rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}
.header__logo-titulo {
  margin: 0;
  color: #4d4c52;
  font-weight: 400;
}
.header__logo-titulo span {
  color: #2d6499;
  font-weight: 900;
  font-size: 5rem;
}

.nav {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.nav__link {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #2d6499;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .nav__link {
    margin-bottom: 0;
  }
}
.nav__link:hover {
  color: #214a72;
}

.slider {
  overflow: hidden;
  position: relative;
}
.slider__slide {
  height: 100%;
  width: 100%;
  flex-shrink: 0;
  position: relative;
  transition-property: transform;
}
.slider__imagen {
  width: 100%;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #FFFFFF;
  width: 4rem;
}
@media (min-width: 768px) {
  .slider .swiper-button-next,
.slider .swiper-button-prev {
    width: 6rem;
  }
}
@media (min-width: 1024px) {
  .slider .swiper-button-next,
.slider .swiper-button-prev {
    width: 8rem;
  }
}
.slider .swiper-button-next::after,
.slider .swiper-button-prev::after {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .slider .swiper-button-next::after,
.slider .swiper-button-prev::after {
    font-size: 3rem;
  }
}
.slider .swiper-pagination-bullet {
  background-color: #FFFFFF;
  opacity: 0.5;
}
.slider .swiper-pagination-bullet-active {
  background-color: #2d6499;
}

.servicios {
  margin-top: 3rem;
  background-color: #f8f8f8;
}
.servicios__heading {
  font-weight: 400;
  color: #4d4c52;
  padding: 5rem 0 0 0;
}
.servicios__heading span {
  font-weight: 700;
}
.servicios__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #2d6499 0%, #2d6499 50%, #5d97cf 50%, #5d97cf 100%);
}
.servicios__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .servicios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.servicio {
  background-color: #FFFFFF;
  max-width: 100%;
  margin-top: 3rem;
  transition-property: transform box-shadow;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .servicio {
    margin-bottom: 5rem;
  }
}
.servicio:last-of-type {
  margin-bottom: 5rem;
}
.servicio:hover {
  box-shadow: 0px 9px 15px 0px rgb(97, 99, 96);
  transform: scale(1.02);
}
.servicio__imagen {
  background-size: cover;
  position: relative;
  padding-left: 2rem;
}
@media (min-width: 768px) {
  .servicio__imagen {
    padding-left: 4rem;
  }
}
.servicio__nombre {
  position: absolute;
  left: 1rem;
  bottom: -3rem;
  color: #2d6499;
  font-size: 2.8rem;
}
.servicio__contenido {
  padding: 2rem;
}
.servicio__texto {
  margin: 0;
  color: #4d4c52;
}
.servicio__enlace {
  background-color: #2d6499;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: #FFFFFF;
  text-transform: uppercase;
  border: none;
  display: block;
  text-transform: uppercase;
}
.servicio__enlace:hover {
  background-color: #275785;
  cursor: pointer;
}

.nosotros {
  margin-top: 5rem;
}
.nosotros__heading {
  font-weight: 400;
  color: #4d4c52;
}
.nosotros__heading span {
  font-weight: 700;
}
.nosotros__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #2d6499 0%, #2d6499 50%, #5d97cf 50%, #5d97cf 100%);
}
.nosotros__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros__texto {
  display: flex;
  align-items: center;
  color: #4d4c52;
}
.nosotros__imagen {
  background-size: cover;
}

.testimoniales-blog {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .testimoniales-blog {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .testimoniales-blog__blog {
    grid-column: 2/4;
  }
}
.testimoniales-blog__heading {
  font-weight: 400;
  color: #4d4c52;
  padding: 5rem 0 0 0;
}
.testimoniales-blog__heading span {
  font-weight: 700;
  display: block;
}
.testimoniales-blog__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #2d6499 0%, #2d6499 50%, #5d97cf 50%, #5d97cf 100%);
}

.testimonial {
  text-align: center;
}
.testimonial__imagen {
  width: 20rem;
  margin: 0 auto;
}
.testimonial__contenido {
  background-color: #FFFFFF;
  margin-top: -10rem;
  padding: 10rem 2rem 2rem 2rem;
}
.testimonial__nombre {
  color: #2d6499;
  font-size: 2.4rem;
  font-weight: 700;
}
.testimonial__texto::before {
  content: "";
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.entrada {
  background-color: #FFFFFF;
  margin-bottom: 2rem;
  transition-property: transform box-shadow;
  transition-duration: 0.3s;
}
.entrada:hover {
  box-shadow: 0px 9px 15px 0px rgb(97, 99, 96);
  transform: scale(1.02);
}
@media (min-width: 768px) {
  .entrada {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
@media (min-width: 768px) {
  .entrada__imagen-contenedor {
    grid-column: 1/3;
  }
}
.entrada__imagen {
  height: 28rem;
  width: 100%;
  object-fit: cover;
}
.entrada__contenido {
  padding: 1rem;
  color: #000000;
}
@media (min-width: 768px) {
  .entrada__contenido {
    grid-column: 3/6;
  }
}
.entrada__meta {
  font-size: 1.4rem;
  color: #a3a4a2;
}
.entrada__enlace {
  background-color: #2d6499;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: #FFFFFF;
  text-transform: uppercase;
  border: none;
  display: block;
}
.entrada__enlace:hover {
  background-color: #275785;
  cursor: pointer;
}
@media (min-width: 768px) {
  .entrada__enlace {
    display: inline-block;
  }
}

.testimoniales__bg {
  background-color: #f8f8f8;
}

.cita {
  background-image: url(../img/bg_citas.jpg);
  background-position: center, center;
  background-size: cover;
  background-attachment: fixed;
  padding: 5rem 0;
  text-align: center;
}
.cita__heading {
  font-weight: 400;
  color: #FFFFFF;
  font-size: 5rem;
}
.cita__texto {
  max-width: 60rem;
  margin: 5rem auto;
  text-align: center;
  color: #FFFFFF;
  line-height: 2;
}
.cita__enlace {
  background-color: #2d6499;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: #FFFFFF;
  text-transform: uppercase;
  border: none;
  padding: 2rem 10rem;
}
.cita__enlace:hover {
  background-color: #275785;
  cursor: pointer;
}

.sociales {
  margin: 5rem 0;
  display: flex;
  filter: grayscale(1);
}
@media (min-width: 768px) {
  .sociales {
    margin: 0;
  }
}
.sociales__enlace::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.sociales__enlace[href*="facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.sociales__enlace[href*="twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.sociales__enlace[href*="instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.sociales__enlace[href*="pinterest.com"]::before {
  background-image: url(../img/icono_pinterest.svg);
}
.sociales__enlace[href*="youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.sociales__enlace[href*="tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.sociales__accesible {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.footer {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__widget {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__heading {
  font-weight: 400;
  color: #4d4c52;
  font-size: 3.4rem;
  text-align: left;
}
.footer__heading span {
  font-weight: 700;
}
.footer__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #2d6499 0%, #2d6499 50%, #5d97cf 50%, #5d97cf 100%);
}
.footer__texto {
  line-height: 1.8;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  text-align: center;
  max-width: 120rem;
  margin: 2rem auto 0 auto;
  border-top: 0.1rem solid #dfdfdf;
  font-size: 1.6rem;
  padding: 2rem;
  color: #a3a4a2;
}