.nav {
    display: flex;
    align-items: center;
    flex-direction: column;
    @include tablet {
        flex-direction: row;
        gap: 2rem;
    }
    &__link {
        font-size: 2rem;
        margin-bottom: 1rem;
        color:  $primario;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include tablet {
            margin-bottom: 0;
        }
        &:hover {
            color:  darken($primario, 10%);
        }
    }
}