.nosotros {
    margin-top: 5rem;    
    &__heading {
        @include heading;        
    }
    &__grid {
        @include grid(1, 2rem);
        @include tablet {
            @include grid(2, 2rem);
        }
    }
    &__texto {
        display: flex;
        align-items: center;
        color: $secundario;
    }
    &__imagen {
        background-size: cover;        
    }
}