.servicios {
    margin-top: 3rem;    
    background-color: $gris;
    
    &__heading {
        @include heading;
        padding: 5rem 0 0 0; 
    }
    &__grid {
        @include grid(1, 2rem);
        @include tablet {
            @include grid(2, 2rem);
        }
        @include desktop {
            @include grid(3, 2rem);
        }
    }
}

.servicio {
    background-color: $blanco;    
    max-width: 100%;
    margin-top: 3rem;     
    transition-property: transform box-shadow;
    transition-duration: .3s;

    @include tablet {
        margin-bottom: 5rem;
    }
    &:last-of-type {
        margin-bottom: 5rem;
    }
    &:hover {
        box-shadow: 0px 9px 15px 0px rgb(97, 99, 96);
        transform: scale(1.02);
    }  
    

    &__imagen {        
        background-size: cover;     
        position: relative;
        padding-left: 2rem;
        @include tablet {
            padding-left: 4rem;
        }
        
    }
    &__nombre {
        position: absolute;
        left: 1rem;
        bottom: -3rem;
        color: $primario;
        font-size: 2.8rem;
        
    }
    &__contenido {
        padding: 2rem;
    }
    &__texto {
        margin: 0;
        color: $secundario;
        
    }
    &__enlace {
        @include boton($primario, $blanco);
        display: block;
        text-transform: uppercase;
        
    }
}