

.header {
      padding: 2rem ;
      background-color: $gris;
      border-bottom: .1rem solid darken($gris, 10%);
      margin-bottom: 3rem;


    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;        
        @include tablet {
            flex-direction: row;
            justify-content: space-between;            
        }
    }
    &__logo {
        margin-bottom: 3rem;        
        @include tablet {
            margin-bottom: 0
        }

        &-titulo {
           margin: 0; 
           color: $secundario;
           font-weight: $regular;
           & span {
                color: $primario;
                font-weight: $black;
                font-size: 5rem;
           }
        }
    }
   
}