/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: bold;
    text-decoration: none;
    padding: 1rem 3rem ;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;    
    text-transform: uppercase;
    border: none;
    

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}

@mixin heading($salto: false) {
    font-weight: 400;
    color: $secundario;
    
    span {
       font-weight: 700; 
       @if($salto) {
           display: block;           
       }
    }    
    &::after {
        content: '';
        display: block;
        height: 1rem;
        width: 15rem;
        margin-top: 1rem;        
        background-image: linear-gradient(to right, $primario 0%, $primario 50%, lighten($primario, 20%) 50%, lighten($primario, 20%) 100%);
    }
}