.testimoniales-blog {
    margin-top: 5rem;   

    @include desktop {
        @include grid(3, 4rem)
    }

    &__blog {
        @include desktop {
            grid-column: 2 / 4;
        }
    }

    &__heading {
        @include heading(true);
        padding: 5rem 0 0 0;

    }
}

.testimonial {
    text-align: center;  

    &__imagen {
        width: 20rem;
        margin: 0 auto;        
    }

    &__contenido {
        background-color: $blanco;
        margin-top: -10rem;
        padding: 10rem 2rem 2rem 2rem;        
    }
    &__nombre {
        color: $primario;
        font-size: 2.4rem;
        font-weight: 700;
    }
    &__texto {        
        &::before {
            content: '';
            background-image: url(../img/quote.svg);
            background-repeat: no-repeat;
            background-position: center;
            display: inline-block;
            width: 4rem;
            height: 4rem;
            margin-right: 1rem;           
            
        }
    }
}

.entrada {
    background-color: $blanco;
    margin-bottom: 2rem;
    transition-property: transform box-shadow;
    transition-duration: .3s;
    &:hover {
        box-shadow: 0px 9px 15px 0px rgb(97, 99, 96);
        transform: scale(1.02);
    }     

    @include tablet {
        @include grid(5, 2rem);
        align-items: center;
    }
    &__imagen-contenedor {
        @include tablet {
             grid-column: 1 / 3;         
        }
    }
    &__imagen {
        height: 28rem;
        width: 100%;
        object-fit: cover;
    }
    &__contenido {
        padding: 1rem;
        color: $negro;        
        @include tablet {
            grid-column: 3 / 6;
        }
    }
    &__meta {
        font-size: 1.4rem;
        color: $grisOscuro;
    }
    &__enlace {
        @include boton($primario, $blanco);
        display: block;
        @include tablet {
            display: inline-block;
        }  
    }
}
.testimoniales__bg {
    background-color: $gris;
    
}